

let defaultState = {
  visible: true,


  isStart: "none",
  log: "",
  record: "none",
  month: 1,
  day: 1,
  hour: 7,
  minute: 1,
  method: "CLT",
  action: "00",
  event: false,
  ampm: "AM",

  bookHour: 0,
  bookMinute: 0,
  bookAmpm: ""
}



const main = (state = defaultState, action) => {
  switch (action.type) {

    

    case 'VISIBLE':
      console.log('data in reducer', action.payload)
      return {
        ...state,
        visible: action.payload
      }

    case "SET_STATE": 
      return {
        ...state,
        [action.payload.name]: action.payload.value
      }
    
    default:
      return state
  }
}

export default main