import React, { ReactElement, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
// import unicorn from './unicorn.png';
import Switcher from './components/Switch';
import Follow from './pages/Follow';
// import Auto from './pages/Auto';
// import Upwork from './pages/Upwork';
import IconButton from '@mui/material/IconButton';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import OutputIcon from '@mui/icons-material/Output';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import AdbIcon from '@mui/icons-material/Adb';
import PersonPinCircleIcon from '@mui/icons-material/PersonPinCircle';
import SettingsSuggestIcon from '@mui/icons-material/SettingsSuggest';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "./panel.css"

export default function Panel({ onWidthChange, initialEnabled }: { onWidthChange: (value: number) => void, initialEnabled: boolean }): ReactElement {

  const dispatch = useDispatch();


  const { visible, currentTime, isStart, log, month, day, hour, minute, method, action, event, ampm, record, bookHour, bookMinute, bookAmpm } = useSelector(state => state.main);

  const setState = (name, value) => {
    dispatch({
      type: "SET_STATE",
      payload: {
        name, value
      }
    })
  }

  const handleMonthChange = (e) => {
    if (e.target.value >= 1 && e.target.value <= 12) {
      setState("month", e.target.value)
    }
  }
  const handleDayChange = (e) => {
    if (e.target.value >= 1 && e.target.value <= 31) {
      setState("day", e.target.value)
    }
  }
  const handleHourChange = (e) => {
    if (e.target.value >= 0 && e.target.value <= 12) {
      setState("hour", e.target.value)
    }
  }
  const handleMinuteChange = (e) => {
    if (e.target.value >= 0 && e.target.value <= 59) {
      setState("minute", e.target.value)
    }
  }

  const sleep = (timeout: number) => new Promise<void>((resolve, reject) => {
    setTimeout(() => {
      resolve()
    }, timeout * 1000);
  })

  /**
   * swith the sidebar
   * @param value visible
   * @VenusWhisperer
  */
  const setVisible = (value) => {
    dispatch({
      type: "VISIBLE",
      payload: value
    })
  }

  const showNotification = (msg: string, type: string) => {
    toast[type](msg, {
      position: type === "error" ? 'bottom-right' : "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
    });
  }

  function _matchCount(str, re) {
    var matches = str.match(re)
    return matches ? matches.length : 0;
  };

  function selectOption(selectElement) {
    // Get the select element by its ID

    // Find and select the desired option by its value
    // for (var i = 0; i < selectElement.options.length; i++) {
    // if (selectElement.options[i].value === valueToSelect) {
    selectElement.selectedIndex = 1;

    // Create a new 'change' event
    var event = new Event('change');

    // Dispatch it on the select element to trigger any attached change event listeners
    selectElement.dispatchEvent(event);
    // break;

    // }
    // }
  }


  const _book = async () => {

    if (window.location.href.indexOf("https://web.foretees.com/v5/clearwaterbaygolf_golf_m24/Member_sheet") === 0) {
      const bookItems = document.querySelectorAll(action === "01" ? ".noRowColor, .hasRowColor" : ".hasRowColor");
      console.log(bookItems, "12435");
      console.log(bookItems.length, '66867');

      if (bookItems.length === 0) {
        showNotification("No available tee Time", "info");
        setState("record", "No available tee Time");
        setState("isStart", "none");
        return;
      }

      let isAvaiable = true;
      for (let i = 0; i < bookItems.length; i++) {

        const item = bookItems[i];

        const membersCount = _matchCount(item.innerHTML.toString(), /CRT/g) + _matchCount(item.innerHTML.toString(), /WLK/g);
        const bCount = _matchCount(item.innerHTML.toString(), /">B</g);

        const timeElement = item.querySelector("div[class='sT rwdTd']")?.firstChild as HTMLElement;

        const splits = timeElement?.innerHTML.toString().split(" ");
        let _hour = parseInt(splits[0].split(":")[0]);
        const _minute = splits[0].split(":")[1];
        const _ampm = splits[1];

        setState("bookHour", _hour);
        setState("bookMinute", _minute);
        setState("bookAmpm", _ampm);

        _hour = (_ampm === "PM" && _hour < 12) ? _hour + 12 : _hour;
        const originHour: number = (ampm === "PM" && parseInt(hour) < 12) ? parseInt(hour) + 12 : parseInt(hour);

        // console.log(_hour, _ampm, originHour);
        if (((_hour == originHour && parseInt(_minute) >= minute) || _hour > originHour) && timeElement.tagName === "A") {
          
          if (action === "01" && membersCount <= 4 && (_matchCount(item.innerHTML.toString(), /background-color:#FF69B4/g) > 0
            || _matchCount(item.innerHTML.toString(), /background-color:#ff74b9/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#00FFFF/g) > 0
            || _matchCount(item.innerHTML.toString(), /background-color:#98ef98/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#12ffff/g) > 0
            || _matchCount(item.innerHTML.toString(), /background-color:#5F9EA0/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#6aa5a7/g) > 0
            || _matchCount(item.innerHTML.toString(), /background-color:#90EE90/g) > 0 || _matchCount(item.innerHTML.toString(), /sT rwdTd/g) > 0
          )) {//membersCount <= 4
            if (_matchCount(item.innerHTML.toString(), /background-color:#FFD700/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#ffda12/g) > 0)              
              {console.log("false");
              continue;}
            else {
              timeElement.click();              
              isAvaiable = false;
              break;
            }
            } else if (action === "10" && (_matchCount(item.innerHTML.toString(), /background-color:#FF69B4/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#ff74b9/g) > 0) && membersCount <= 4) {
            isAvaiable = false;
            timeElement.click();
            break;
          } else if (action === "00" && membersCount <= 6) {
            isAvaiable = false;
            timeElement.click();
            break;
          }
        }
      }

      if (!isAvaiable) {
        return;
      }

      for (let i = bookItems.length - 1; i >= 0; i--) {
        
        sleep(5)
        const item = bookItems[i];
        
        
        const membersCount = _matchCount(item.innerHTML.toString(), /CRT/g) + _matchCount(item.innerHTML.toString(), /WLK/g);
        const bCount = _matchCount(item.innerHTML.toString(), /">B</g);

        const timeElement = item.querySelector("div[class='sT rwdTd']")?.firstChild as HTMLElement;

        const splits = timeElement?.innerHTML.toString().split(" ");
        let _hour = parseInt(splits[0].split(":")[0]);
        const _minute = splits[0].split(":")[1];
        const _ampm = splits[1];

        setState("bookHour", _hour);
        setState("bookMinute", _minute);
        setState("bookAmpm", _ampm);

        _hour = (_ampm === "PM" && _hour < 12) ? _hour + 12 : _hour;
        const originHour: number = (ampm === "PM" && parseInt(hour) < 12) ? parseInt(hour) + 12 : parseInt(hour);

        console.log(_hour, _ampm, originHour);

        if (timeElement.tagName === "A") {
          // console.log("target: ", `${hour}:${minute} ${ampm}`)
          // console.log("cur: ", `${_hour}:${_minute} ${_ampm}`)
          // console.log(membersCount, bCount);
          // console.log(timeElement?.tagName)
          console.log('s=====');
          
          if (action === "01" && membersCount <= 4 && (_matchCount(item.innerHTML.toString(), /background-color:#FF69B4/g) > 0
              || _matchCount(item.innerHTML.toString(), /background-color:#ff74b9/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#00FFFF/g) > 0
              || _matchCount(item.innerHTML.toString(), /background-color:#98ef98/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#12ffff/g) > 0
              || _matchCount(item.innerHTML.toString(), /background-color:#5F9EA0/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#6aa5a7/g) > 0
              || _matchCount(item.innerHTML.toString(), /background-color:#90EE90/g) > 0 || _matchCount(item.innerHTML.toString(), /sT rwdTd/g) > 0
          )) {//membersCount <= 4
              if (_matchCount(item.innerHTML.toString(), /background-color:#FFD700/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#ffda12/g) > 0)              
                {console.log("false");
                continue;}
              else {
                timeElement.click();              
                isAvaiable = false;
                break;
              }
          } else if (action === "10" && (_matchCount(item.innerHTML.toString(), /background-color:#FF69B4/g) > 0 || _matchCount(item.innerHTML.toString(), /background-color:#ff74b9/g) > 0) && membersCount <= 4) {
            isAvaiable = false;
            timeElement.click();
            break;
          } else if (action === "00" && membersCount <= 6) {
            isAvaiable = false;
            timeElement.click();
            break;
          }
        }
      }

      if (isAvaiable) {
        showNotification("No available tee Time", "info");
        setState("record", "No available tee Time");
        setState("isStart", "none");
        return;
      }

    } else if (window.location.href.indexOf("https://web.foretees.com/v5/clearwaterbaygolf_golf_m24/Member_slot") === 0) {
      const _yesButton = document.querySelector("div[class=\"ui-dialog-buttonset\"]")?.childNodes[1];
      if (_yesButton) {
        _yesButton.click();
        await sleep(2);
      }

      setState("isStart", "none");
      if (action === "01") {
        await sleep(1);
        const listItem = document.querySelector("div[class='ftMs-listItem']");

        if (listItem) {
          listItem.click();
        }

        let _action = "With wife";
        showNotification("OK", "info");
        setState("record", `${bookHour}:${bookMinute} ${bookAmpm} ${_action}`);
        const _submit = document.querySelector("a[class='submit_request_button']");
        _submit?.click();

        await sleep(1);

        const alert2 = document.querySelector('body > div:nth-of-type(6) > div:nth-of-type(4) > div > button:nth-of-type(2)');
        if (alert2) {
          alert2.click();
          await sleep(3);
        }
        await sleep(3)

      } else if (action === "00") {

        let _action = "Alone";
        showNotification("OK", "info");
        setState("record", `${bookHour}:${bookMinute} ${bookAmpm} ${_action}`);

        const _submit = document.querySelector("a[class='submit_request_button']")
        _submit?.click();
      } else if (action === "10") {
        document.querySelector("div[data-fttab=\".ftMs-guestTypes\"]")?.click();
        await sleep(0.2);

        const _wrapper = document.querySelector("div[class=\"ftMs-block ftMs-guestTypes active\"]");
        const _btn = _wrapper?.querySelector("div[class='ftMs-listItem']");

        for (let i = 0; i < 3; i++) {
          _btn?.click();
          await sleep(0.2);
        }

        await sleep(0.5);
        const _selectes = document.querySelectorAll(".transport_type:not([disabled=\"disabled\"])");
        // await sleep(0.5);
        for (let i = 0; i < _selectes.length; i++) {
          selectOption(_selectes[i]);
          await sleep(0.2);
        }

        await sleep(0.2);

        let _action = "with Guests";
        showNotification("OK", "info");
        setState("record", `${bookHour}:${bookMinute} ${bookAmpm} ${_action}`);
        const _submit = document.querySelector("a[class='submit_request_button']")
        _submit?.click();

      }

      await sleep(1);
      const alert = document.querySelector("div[class='ui-dialog-buttonset']");
      if (alert) {
        alert?.lastChild?.click();
        await sleep(2);
      }

      let _action = "Alone";
      if (action === "01") {
        _action = "With wife";
      } else if (action === "10") {
        _action = "With guests";
      }

      showNotification("OK", "info");
      setState("record", `${bookHour}:${bookMinute} ${bookAmpm} ${_action}`);

      await sleep(2)
      // document.querySelector("div[class=\"ui-dialog-buttonset\"]")?.firstChild.click();
      window.location.href = "https://web.foretees.com/v5/clearwaterbaygolf_golf_m24/Member_select";
    }
  }
  React.useEffect(() => {

    if (!window.location.href || isStart === "none") {
      return;
    }

    _book();
  }, [window.location.href]);

  const handleBook = async () => {

    try {
      console.log(month, day, hour, minute, ampm, method, action, event)

      const refresh = document.querySelector("a[title='Refresh']");
      refresh?.click();

      sleep(5000);
      const elements = document.querySelectorAll("a[class='ui-state-default']");
      elements.forEach(item => {
        if (item.innerHTML === String(day)) {
          item?.click();
        }
      });


    } catch (err) {
      console.log(err)
    }

  }

  const handleStart = async (delay: number) => {
    // console.log(delay);
    setState("log", `Book set for: ${hour}:${minute} ${ampm} ( wait unitil 9:30 AM )`);
    showNotification("Book is set successfully...", "success");
    setState("isStart", delay.toString());

    setState("record", "Not yet");
    
    // // Get the local time
    // const currentDate = new Date();
    // // Get the local date and month
    // const date = currentDate.getDate();
    // const month = currentDate.getMonth() + 1; // Month is zero-based, so we add 1

    // console.log("Local date is: " + date);
    // console.log("Local month is: " + month);
    // if (month ==3 && date <= 15){
    // handleBook();
    // }
      
    
  }

  const reset = () => {
    setState("isStart", "none");
  }

  React.useEffect(() => {

    const id = setInterval(async () => {

      const div_clock = document.querySelector("b[class='jquery_server_clock']");

      if (!div_clock) {
        return;
      }

      const time_string = div_clock?.innerHTML;

      const time = time_string.split(" ")[0];
      const ampm = time_string.split(" ")[1];

      const time_split = time.split(":");

      const hour = time_split[0];
      const minute = time_split[1];
      const second = time_split[2];

      setState("currentTime", `${hour}:${minute}:${second}   ${ampm}`);

      console.log(hour, minute, ampm, isStart)
      if (parseInt(hour) == 9 && parseInt(minute) >= 30 && ampm == "AM" && isStart != "none") {
        console.log("fire..")      

        handleBook();
      }
    }, 1000);

    const refreshId = setInterval(() => {

      if (isStart === "none") {
        const refresh = document.querySelector("a[title='Refresh']");
        refresh?.click();
      }
    }, 300 * 1000);

    return () => {
      clearInterval(id);
      clearInterval(refreshId);
    }
  }, [isStart]);



  return (
    <>
      <div
        className={`sider-container${!visible ? ' hide-right' : ''}`}
      >
        <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        <Box pt={5} pl={5} pr={11} >
          <Typography><font style={{ fontSize: 14 }}>current Time:</font> {currentTime}</Typography>
          <Typography mt={5}>Booking Setting</Typography>
          <Grid container pt={2}>
            <Grid item xs={4}>
              <TextField
                label="Month"
                type='number'
                size='small'
                value={month}
                onChange={handleMonthChange}
              />
            </Grid>
            <Grid item xs={4} pl={1}>
              <TextField
                label="Day"
                type='number'
                size='small'
                value={day}
                onChange={handleDayChange}
              />
            </Grid>
          </Grid>
          <Grid container pt={2}>
            <Grid item xs={4}>
              <TextField
                label="Hour"
                type='number'
                size='small'
                value={hour}
                onChange={handleHourChange}
              />
            </Grid>
            <Grid item xs={4} pl={1}>
              <TextField
                label="Minute"
                type='number'
                size='small'
                value={minute}
                onChange={handleMinuteChange}
              />
            </Grid>
            <Grid item xs={4} pl={1}>
              <select
                aria-label="demo-simple-select-label"
                value={ampm}
                onChange={e => setState("ampm", e.target.value)}
                className='custom-select'
                style={{ paddingLeft: 10, width: '100%' }}
              >
                <option value="AM">AM</option>
                <option value="PM">PM</option>
              </select>
            </Grid>
            <Box width={'100%'} mt={2}>
              <select

                aria-label="demo-simple-select-label"
                value={method}
                onChange={e => setState("method", e.target.value)}
                className='custom-select'
                style={{ paddingLeft: 10, width: '100%' }}
              >
                <option value="CRT">CRT</option>
                <option value="WLK">WLK</option>
              </select>
            </Box>
            <Box width={'100%'} mt={2}>
              <select

                aria-label="demo-simple-select-label"
                value={action}
                onChange={e => setState("action", e.target.value)}
                className='custom-select'
                style={{ paddingLeft: 10, width: '100%' }}
              >
                <option value="01">With Wife</option>
                <option value="00">Alone</option>
                <option value="10">With Guests</option>
              </select>
            </Box>
          </Grid>

          <Button onClick={() => handleStart(0)} sx={{ width: '100%', mt: 4 }} variant="contained" size="small">Start</Button>
          <Button onClick={() => reset()} sx={{ width: '100%', mt: 1 }} variant="contained" size="small">reset</Button>

          <div style={{
            border: '1px solid #0000ee55',
            marginTop: 20,
            borderRadius: 4,
            padding: 5,
            maxHeight: '80vh',
            overflowY: 'scroll',
            minHeight: 100
          }}>
            <Typography fontSize={13} lineHeight={1.5}>
              for Book: {
                isStart === "none" ? "no book" : log
              }
            </Typography>
            <Typography fontSize={13} lineHeight={1.5}>
              set Book: {record}
            </Typography>
          </div>

        </Box>
        <div className='sider'>
          <IconButton size='small' aria-label="delete" onClick={() => setVisible(false)}>
            <OutputIcon />
          </IconButton>
        </div>
        <div className={`switch-button ${visible && 'none'}`} onClick={() => setVisible(!visible)}><SettingsSuggestIcon style={{ color: '#0000ffaa', fontSize: 30 }} /></div>
      </div>
    </>
  );
}
